import openUserFrom from './userCenter/userFrom';
import { managerApi } from '@/api/api';
export default {
  components: {
    openUserFrom: openUserFrom
  },
  data: function data() {
    return {
      FormData: {
        data: {
          kw: "",
          pageSize: 20,
          page: 1
        },
        rules: {}
      },
      table: {
        total: 0,
        pageSize: 20,
        page: 1,
        loading: false,
        height: "515",
        columns: [{
          title: '序号',
          type: "index",
          width: 80,
          align: "center"
        }, {
          title: "用户名",
          align: "center",
          key: "discount"
        }, {
          title: "头像",
          align: "center",
          slot: "userimg",
          key: "userimg"
        }, {
          title: "姓名",
          align: "center",
          key: "userName"
        }, {
          title: "性别",
          align: "center",
          slot: 'gender',
          key: "gender"
        }, {
          title: "年级",
          align: "center",
          tooltip: true,
          key: "userGrade"
        }, {
          title: "班级",
          align: "center",
          tooltip: true,
          key: "userClass"
        }, {
          title: "角色",
          align: "center",
          tooltip: true,
          slot: 'userRole',
          key: "userRole"
        }, {
          title: "手机号",
          align: "center",
          tooltip: true,
          key: "telephone"
        }, {
          title: "状态",
          align: "center",
          slot: 'status',
          tooltip: true,
          key: "status"
        }, {
          title: "操作",
          slot: "action",
          width: 280,
          align: "center"
        }],
        tableData: []
      }
    };
  },
  mounted: function mounted() {
    this.getList();
  },
  methods: {
    openUserFrom: function openUserFrom() {
      this.$refs.openUserFrom.init('add', '');
    },
    handleAddNewUser: function handleAddNewUser(data, type) {
      var _this = this;

      if (type === 'add') {
        this.addNewUserAjax(data);
      } else if (type === 'edit') {
        this.table.loading = true;
        managerApi.updateUserData({
          _id: data._id,
          userName: data.userName,
          discount: data.discount,
          password: data.password,
          telephone: data.telephone,
          gender: data.gender,
          userRole: data.userRole,
          userGrade: data.userGrade,
          userClass: data.userClass,
          userimg: data.userimg,
          status: data.status,
          stuID: data.stuID,
          major: data.major,
          isOnline: data.isOnline
        }).then(function (res) {
          _this.Message("success", res.data.msg);

          _this.getList();

          _this.table.loading = false;
        }).catch(function (err) {
          _this.Message("error", err.data.msg);

          _this.getList();

          _this.table.loading = false;
        });
      }
    },
    searchFn: function searchFn() {
      this.table.page = 1;
      this.getList();
    },
    // 编辑人员
    editFn: function editFn(row) {
      var _this2 = this;

      managerApi.getUserData({
        _id: row._id
      }).then(function (res) {
        _this2.Message("success", res.data.msg);

        _this2.$refs.openUserFrom.init("edit", res.data.data[0]);
      }).catch(function (err) {
        _this2.Message("error", err.data.msg);
      });
    },
    // 添加人员
    addNewUserAjax: function addNewUserAjax(data) {
      var _this3 = this;

      this.table.loading = true;
      managerApi.addUser({
        data: data
      }).then(function (res) {
        _this3.getList();

        _this3.Message('success', res.data.msg);

        _this3.table.loading = false;
      }).catch(function (err) {
        _this3.getList();

        _this3.Message('error', err.data.msg);

        _this3.table.loading = false;
      });
    },
    // 重置搜索
    resetFn: function resetFn() {
      this.$refs.formList.resetFields();
    },
    // pageSize改变
    pageSizeChange: function pageSizeChange(value) {
      this.table.pageSize = value;
      this.getList();
    },
    // page改变
    pageChange: function pageChange(value) {
      this.table.page = value;
      this.getList();
    },
    // 查询列表数据
    getList: function getList() {
      var _this4 = this;

      this.FormData.data.pageSize = this.table.pageSize;
      this.FormData.data.page = this.table.page;
      this.table.loading = true;
      managerApi.getList(this.FormData.data).then(function (res) {
        _this4.table.tableData = res.data.info.list;
        _this4.table.total = res.data.info.count;
        _this4.table.loading = false;
      }).catch(function (err) {
        _this4.Message('error', err.data.msg);

        _this4.table.loading = false;
      });
    },
    delFn: function delFn(row) {
      var _this5 = this;

      this.$Modal.confirm({
        title: "批量删除",
        content: "<p>确定要删除吗？</p>",
        onOk: function onOk() {
          _this5.delFnAjax(row._id);
        },
        onCancel: function onCancel() {
          _this5.Message("info", "取消删除");
        }
      });
    },
    // 单个删除Ajax
    delFnAjax: function delFnAjax(_id) {
      var _this6 = this;

      managerApi.delData({
        ids: _id
      }).then(function (res) {
        _this6.Message("success", res.data.msg);

        _this6.getList();

        _this6.table.loading = false;
      }).catch(function (err) {
        _this6.Message("error", err.data.msg);

        _this6.getList();

        _this6.table.loading = false;
      });
    },
    // 封装消息提示
    Message: function Message(type, content, duration, closable) {
      var msDuration, msClosable;

      if (duration === null || duration === undefined || duration === '') {
        msDuration = 1.5;
      } else {
        msDuration = duration;
      }

      if (closable === null || closable === undefined || closable === '') {
        msClosable = false;
      } else {
        msClosable = closable;
      }

      this.$Message.destroy();
      this.$Message[type]({
        content: content,
        duration: msDuration,
        closable: msClosable
      });
    }
  }
};