export default {
  data: function data() {
    return {
      modalData: {
        title: "添加用户",
        modalBoolean: false
      },
      nowType: "",
      isDisabled: false,
      rePassword: "",
      FormData: {
        rules: {},
        data: {
          userName: "",
          discount: "",
          password: "",
          telephone: "",
          gender: "0",
          userRole: "1",
          userGrade: "",
          userClass: "",
          userimg: "",
          status: "1",
          stuID: "",
          major: "",
          isOnline: false
        }
      }
    };
  },
  methods: {
    init: function init(type, data) {
      this.nowType = type;

      if (type === "edit") {
        this.isDisabled = true;
        this.modalData.title = "更新用户信息";
        this.FormData.data = data;
      } else if (type === "add") {
        this.isDisabled = false;
        this.$refs.formList.resetFields();
        this.modalData.title = "添加用户";
      } else if (type === "show") {
        this.FormData.data = data;
        this.modalData.title = "查看用户";
      }

      this.modalData.modalBoolean = true;
    },
    closeModal: function closeModal() {
      this.modalData.modalBoolean = false;
    },
    addNewQS: function addNewQS() {
      this.$emit("callBack", this.FormData.data, this.nowType);
      this.modalData.modalBoolean = false;
    }
  }
};